import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";

import Image from "./img/01.png";
import Image2 from "./img/3801-710001-1.webp";
import Image3 from "./img/03.png";

import Countbutton from "./countbutton";

const libraries = [
  {
    img: Image,
    name: "日向夏ドリンク",
    text:
      "すっきりと爽やかなドリンクに仕上げました｡他の柑橘では味わえない独特な芳香と爽やかな酸味が特徴です。"
  },
  {
    img: Image2,
    name: "リンゴジュース",
    text:
      "自然豊かな安曇野地域の畑しっかり熟したりんご大事に大事に熟成をかけて甘みを引き出したストレート果汁100％のりんごジュース"
  },
  {
    img: Image3,
    name: "グレープフルーツジュース",
    text:
      "希少な国産グレープフルーツを丸ごと搾った、酸味が爽快なストレートジュース。"
  }
];

export default function MediaCard() {
  return (
    <div className="container">
      {libraries.map((library) => (
        <Card sx={{ maxWidth: 250 }} key={library.name}>
          <CardMedia
            sx={{ height: 200 }}
            image={library.img}
            title="green iguana"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              {library.name}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {library.text}
            </Typography>
          </CardContent>
          <Countbutton />
        </Card>
      ))}
    </div>
  );
}
